      registerAddr: "",
      registerStatus: "",
      assistantName: "",
      assistantSex: "",
      assistantContact: "",
      specification: ""
    };
  },
  methods: {
    async onSubmit(e) {
      let parmas = {
        real_name: this.real_name,
        district: this.district,
        sex: this.sex,
        userType: this.userType,
        disabledClass: this.disabledClass,
        idCard: this.idCard,
        address: this.address,
        disabledContact: this.disabledContact,
        area: this.area,
        familyStructure: this.familyStructure,
        familySituation: this.familySituation,
        registerAddr: this.registerAddr,
        registerStatus: this.registerStatus,
        assistantName: this.assistantName,
        assistantSex: this.assistantSex,
        assistantContact: this.assistantContact,
        specification: this.specification
      };
      let res = await updateDisabledByName(parmas);
      if (res.data.errmsg == "ok") alert("修改成功");
    },
    async querybyname(name) {
      let res = await queryDisabledByName(name);
      (this.real_name = res[0].real_name),
        (this.district = res[0].district),
        (this.sex = res[0].sex),
        (this.userType = res[0].userType),
        (this.disabledClass = res[0].disabledClass),
        (this.idCard = res[0].idCard),
        (this.address = res[0].address),
        (this.disabledContact = res[0].disabledContact),
        (this.area = res[0].area),
        (this.familyStructure = res[0].familyStructure),
        (this.familySituation = res[0].familySituation),
        (this.registerAddr = res[0].registerAddr),
        (this.registerStatus = res[0].registerStatus),
        (this.assistantName = res[0].assistantName),
        (this.assistantSex = res[0].assistantSex),
        (this.assistantContact = res[0].assistantContact),
        (this.specification = res[0].specification);
    }
  },
  mounted() {
    this.querybyname(this.$route.query.real_name);
  },
  components: {
    LayOut
  }
};

